body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.download-button 
{
  position: absolute;
  right: 30px;
  opacity: 0;
  transform: translateY(-100%);
  transition: transform ease-in .5s, opacity ease-in .5s, color .2s ease-in-out;
  cursor:pointer;
}

.download-button:hover {
  color: white;
}

.logs:hover > .download-button {
  opacity: 1;
  transform: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  word-break: break-word;
}
.list-wrapper {
  padding: 200px;
}

.modalLogsDialog .bx--modal-container{
  height: 80%;
}

.bx--progress-label{
  margin-right: 0 !important;
  padding-right: 0 !important;
}

.bx--structured-list{
  margin-bottom: 1rem;
}

.modalLogsDialogContainer{
  display: flex;
  flex-direction: column;
  height: 95%;
}

.logs{
  flex-grow: 1;
  white-space: pre-wrap;
  background-color: #121212;
  color: grey;
  padding: 20px;
  margin-bottom: 1rem;
}